@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400..900&display=swap');
* {
    font-family: 'Roboto', sans-serif;
    font-family: "Maven Pro", sans-serif;
    scroll-behavior: smooth;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-align: center;
    /*color: #001C30; */
}

ul {
    list-style: none;
}

body {
    color: #001C30;
}

body.night-mode {
    background-color: #161616;
    background-color: rgb(25, 25, 25);
    color: #fff;
}

.mobile-br {
    display: none;
}

.items__list {
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    padding-top: 12.5vh;
    padding-bottom: 0.25rem;
    border-bottom: 2px solid #001C30;
    font-weight: 600;
}


/* SELECT VIEW */

.select-view-options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: auto;
    margin: 0 4rem 3% auto;
}

.select-view-options .select-option {
    font-size: 1.55rem;
    cursor: pointer;
}

.select-view-options .select-option:first-of-type {
    margin-right: 1rem;
    font-size: 1.9rem;
}

.item__remove {
    /*order: 4; */
    order: -1;
    margin-left: auto;
    margin-right: 10%;
}


/*! FOOD CATEGORIES */

.food_categories {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: 80%;
    margin: 15vh auto 0;
}

.food_categories-container {
    display: flex;
    flex-direction: column;
    width: 45%;
}

.food_categories-container h2 {
    text-align: left;
    color: #000;
    font-size: 2.4rem;
    font-weight: 700;
    line-height: 48px;
}

.food_categories-container p {
    font-weight: 500;
    margin: 0.5rem 0 1.25rem;
    text-align: left;
}

.food_categories-container button {
    line-height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    padding: 12px;
    width: 160px;
    height: 48px;
    outline: none;
    border: 1px solid #000;
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
    background-color: #000;
    color: #fff;
}

.categories_list {
    margin-top: 1.5rem;
    width: 95%;
}

.categories_list:first-of-type {
    margin-top: 2.75rem;
}

.category_list-info {
    width: 57.5%;
}

.category_list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid #bfbfbf;
    padding-bottom: 0.75rem;
}

.category_list-item img {
    background: rgba(0, 0, 0, 0.05);
    height: 60px;
    width: 60px;
    border-radius: 50%;
    padding: 8px;
    user-select: none;
}

.category_list-item .category-name {
    font-size: 1.25rem;
    color: #000;
    margin: 0;
}

.category_list-item .category-description {
    color: rgba(0, 0, 0, 0.50);
    font-size: 15.5px;
    margin: 0.5rem 0 0;
    user-select: none;
}

.category_list-item .item-number {
    font-weight: 600;
    font-size: 1.15rem;
    color: #000;
    width: 20%;
    text-align: right;
}


/*! DATA CHARTS */

.data_charts {
    color: #000;
    margin-top: 15vh;
}

.data_charts h2 {
    font-size: 2.6rem;
}

.data_charts-info {
    font-size: 1.2rem;
    margin-top: 24px;
}

.products_stats {
    display: flex;
    width: 70%;
    justify-content: space-between;
    align-items: center;
    margin: 2.5rem auto 5rem;
}

.products_stats .single-info {
    padding: 16px 18px;
    text-align: left;
    width: 32%;
    border-radius: 6px;
    border: 1px solid rgba(0, 0, 0, 0.3);
}

.single-info p {
    color: rgba(0, 0, 0, 0.55);
    display: flex;
    font-size: 1.3rem;
}

.single-info span {
    display: flex;
    font-size: 2.3rem;
    font-weight: 600;
    margin: 0.75rem 0 0;
}

.radial_chart-pie {
    width: 80%;
    margin: 0 auto;
}

.radial_chart-pie h2 {
    font-size: 2rem;
}


/*! FOOTER */

footer {
    display: flex;
    width: 40%;
    padding-top: 5vh;
    margin: 0 auto 3vh;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

footer p {
    color: #000;
    font-weight: 500;
    font-size: 1.2rem;
    cursor: pointer;
}


/*! MEDIA QUERIES */

@media screen and (max-width:767px) {
    .remove__item {
        width: 30px;
        min-height: 30px;
        line-height: normal;
        padding: 0;
    }
    .mobile-br {
        display: flex;
    }
}